import styled, {css} from 'styled-components/native';
import type {ButtonRootProps} from '../../types/ButtonRootProps';
import * as TOKENS from '@mobile/tokens';

const modifiers = {
  ['primary-red']: () => css`
    background-color: ${TOKENS.colors.red.primary};
  `,
  ['primary-green']: () => css`
    background-color: ${TOKENS.colors.green.primary};
  `,
  ['primary-pear']: () => css`
    background-color: ${TOKENS.colors.pear.primary};
  `,
  ['primary-gray']: () => ({}),
  ['outline-primary-red']: () => ({}),
  ['outline-primary-green']: () => ({}),
  ['outline-primary-pear']: () => ({}),
  ['outline-primary-gray']: () => ({}),
  ['disabled']: () => ({}),
};

export const StyledButton = styled.TouchableOpacity<ButtonRootProps>`
  ${props => css`
    padding: ${TOKENS.spacings.small}px;
    border-radius: ${TOKENS.spacings.small}px;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    ${props.variant === 'primary-red' && modifiers['primary-red']()}
    ${props.isLoading || (props.disabled && modifiers.disabled())}
  `}
`;
